import { Container, Typography } from "@mui/material";

function Etusivu() {
    return(
        <Container style={{textAlign:"center",padding:"50px"}}>
        <Typography variant="h4">Tervetuloa vitsigeneraattoriin!</Typography>
        <br/>
        <Typography variant="h5">Lisää vitsi -näkymässä voit kirjata palveluun oman vitsisi.</Typography>
        <br/>
        <Typography variant="h5">Vitsikortit -näkymässä pääset lukemaan vitsejä satunnaisessa järjestyksessä.</Typography>
        <br/>
        <Typography variant="h5">Ja viimeisenä Lista -näkymässä on listattuna jokainen kirjattu vitsi, joita pääset myös muokkaamaan tai halutessasi poistamaan.</Typography>
        </Container>
    );
}
export default Etusivu;