import {v4 as uuid }from 'uuid';
import UusiVitsi from "./components/UusiVitsi";
import VitsiLista from "./components/VitsiLista";
import { Container } from "@mui/material";
import { useState, useEffect} from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Valikko from "./components/Valikko";
import Poista from "./components/poista";
import Muokkaa from "./components/muokkaa";
import Etusivu from "./components/etusivu";
import RandomVitsi from "./components/randomVitsi"

function App() {

const [vitsit, setVitsit] = useState([
  {
    id : uuid(),
    kysymys : "Miksi niin harvat poistuvat Islannista?",
    vastaus : "Koska se on Tulen ja Jään maa."
  },
  {
    id : uuid(),
    kysymys : "Mitä Jumala sanoi, kun sai lahjaksi lumilaudan?",
    vastaus : "Ui juma lauta!"
  },
  {
    id : uuid(),
    kysymys : "Mitä espanjalainen sanoi nähtyään hyvän Power Point -esityksen?",
    vastaus : "Buenos dias!"
  },
  {
    id : uuid(),
    kysymys : "Mikä on pilates suomeksi?",
    vastaus : "Melilosvot."
  },
  {
    id : uuid(),
    kysymys : "Miksi anopille ei kerrota vitsejä?",
    vastaus : "Koska nauru pidentää ikää."
  },
  {
    id : uuid(),
    kysymys : "Mitä nörtti tekee halutessaan raitista ilmaa?",
    vastaus : "Avaa Windowsin."
  },
  {
    id : uuid(),
    kysymys : "Mikä on majavien joululaulu?",
    vastaus : "Joulupuu on nakerrettu."
  },
  {
    id : uuid(),
    kysymys : "Mikä on halvinta Alkossa mikä menee päähän?",
    vastaus : "Muovipussi."
  },
  {
    id : uuid(),
    kysymys : "Mitä yhteistä on veronkiertäjällä ja vegaanilla?",
    vastaus : "Kumpikaan ei halua maksaa."
  },
]);

  const tallennaVitsit = () => {
    localStorage.setItem("vitsilista", JSON.stringify(vitsit));
  }

  const avaaVitsit = () => {
    if (localStorage.getItem("vitsilista")) {
      setVitsit(JSON.parse(localStorage.getItem("vitsilista")));
    } 
  }

  useEffect(() => {
    if(JSON.parse(localStorage.getItem("vitsilista")) === null) {
      setVitsit(vitsit);
    }
    else{
    avaaVitsit();}
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    tallennaVitsit();
     // eslint-disable-next-line 
  }, [vitsit]); 

  return (
    <BrowserRouter>
      <Container maxWidth="sm">
        <Valikko/>
          <Routes>
            <Route path="/" exact element={<>
              <Etusivu vitsit={vitsit} setVitsit={setVitsit}></Etusivu>
              </>}>
            </Route>
            <Route path="/lista" exact element={<>
              <VitsiLista vitsit={vitsit} setVitsit={setVitsit} ></VitsiLista>
              </>}>
            </Route>
            <Route path="/uusivitsi" element={<>
              <UusiVitsi vitsit={vitsit} setVitsit={setVitsit}></UusiVitsi>
              </>}>  
            </Route> 
            <Route path="/randomvitsi" element={<>
              <RandomVitsi vitsit={vitsit} setVitsit={setVitsit}></RandomVitsi>
              </>}>
            </Route> 
            <Route path="/poista/:id" element={<>
              <Poista vitsit={vitsit} setVitsit={setVitsit}></Poista>
              </>}>
            </Route>
            <Route path="/muokkaa/:id" element={<>
              <Muokkaa vitsit={vitsit} setVitsit={setVitsit}></Muokkaa>
              </>}>
            </Route>
          </Routes>
        </Container>
    </BrowserRouter>
  );
}
export default App;