import { Typography, Button, Container, ButtonGroup } from "@mui/material";
import { useState } from "react";

function Randomvitsi(props) {

    const [sekasin, setSekasin] = useState([]);
    const [nakyma, setNakyma] = useState(false);
    const [idx, setIdx] = useState(0);
    const [paljasta, setPaljasta] = useState(false);
    const [virheilmoitus, setVirheilmoitus] = useState("");
    
//sekoitetaan arrayn tiedot
    const sekoita = () => {
        if(!props.vitsit.length){
            setVirheilmoitus("Vitsilista on tyhjä!");
        }else {
        setSekasin(props.vitsit.sort(() => Math.random()-0.5));
        setIdx(0);
        console.log(sekasin);
        setNakyma(true);
        }
    }
//funktiot joilla suoritetaan korttien vaihdot
    const vaihda = () => {
        if(idx === sekasin.length - 1) {
            setNakyma(false);
            setPaljasta(false);
        }
        else {
            setIdx(idx + 1);
            setPaljasta(false);
        }  
    }
    const vaihdaEdellinen = () => {
        if(idx === 0) {
            setNakyma(false);
            setPaljasta(false);
        }
        else {
            setIdx(idx - 1);
            setPaljasta(false);
        }
    }
    const nayta = () => {
        setPaljasta(true);
    }
    
    return(
    <Container style={{textAlign:"center", padding:"40px"}}>
        <Typography variant="h4">Vitsikortit</Typography>
        <br/>
        {(nakyma)
        ?<><br/>
        <Typography>{idx + 1} / {sekasin.length}</Typography>
        <br/>
        <Typography variant="h5">{sekasin[idx].kysymys}</Typography>
        <br/>
            {(paljasta)
            ?<Typography variant="h5">{sekasin[idx].vastaus}</Typography>
            :<Button color="success" variant="contained" onClick={nayta}>Paljasta vastaus</Button>
            }
        <br/>
        <br/>
        <ButtonGroup variant="contained">
            <Button onClick={vaihdaEdellinen}>Edellinen</Button>
            <Button onClick={vaihda}>Seuraava</Button>
        </ButtonGroup>
        <br/>
        </>
        :<><Button variant="contained" onClick={sekoita} >Aloita!</Button>
        <Typography color="error" variant="h4">{virheilmoitus}</Typography></>
        }
    </Container>
    );
}
export default Randomvitsi;