import { Button,  TextField } from '@mui/material';
import { useRef, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';

function Muokkaa(props){
    
    const navigoi = useNavigate();
    const { id } = useParams();

    const muokattavaVitsi = props.vitsit.filter((vitsit) => {
            return (vitsit.id === id);
        })[0];

    const lomake = useRef({});
    const [kysymys, setKysymys] = useState(muokattavaVitsi.kysymys);
    const [vastaus, setVastaus] = useState(muokattavaVitsi.vastaus);

    const [virheilmoitus, setVirheilmoitus] = useState({});
    
    const kasitteleLomake = (e) => {
        
        e.preventDefault();
        let virhe = {};
           
        if (!kysymys) {
            virhe = {...virhe, kysymys : "Kysymys puuttuu"}
        } 
        if (!vastaus) {
            virhe = {...virhe, vastaus : "Vastaus puuttuu"}
        }

        if (Object.entries(virhe).length > 0) {
            setVirheilmoitus({...virhe});
        } else {
            setVirheilmoitus({}); 
            let muokattuVitsi = {
                                id : id,
                                kysymys : kysymys,
                                vastaus : vastaus,
                                
                            }
            let vitsitApu = props.vitsit.filter((vitsi) => {
                return (vitsi.id !== id);
                });
            props.setVitsit([...vitsitApu, muokattuVitsi]);
            navigoi("/lista");
            }
        }

        const syoteKasittelija = (e) => {
            let arvo = e.target.value;

            if(e.target.name === "kysymys"){
            lomake.current[e.target.name] = arvo;
                setKysymys(arvo);
            }
            else if(e.target.name === "vastaus") {
                lomake.current[e.target.name] = arvo;
                setVastaus(arvo);
            }
            }

    return (
            <form onSubmit={kasitteleLomake}>
            <TextField 
                label="Kysymys"
                name="kysymys"
                variant="outlined"
                fullWidth={true}
                defaultValue={muokattavaVitsi.kysymys}
                style={{marginBottom : 10, marginTop: 10}}
                error={Boolean(virheilmoitus.kysymys)}
                helperText={virheilmoitus.kysymys}
                onChange={ syoteKasittelija }
                multiline
            />
            <TextField 
                label="Vastaus"
                name="vastaus"
                variant="outlined"
                fullWidth={true}
                defaultValue={muokattavaVitsi.vastaus}
                style={{marginBottom : 10, marginTop: 10}}
                error={Boolean(virheilmoitus.vastaus)}
                helperText={virheilmoitus.vastaus}
                onChange={ syoteKasittelija }
                multiline
            />
            <Button 
                type="submit"
                variant="contained"
                fullWidth
                style={{marginBottom : 10}}
            >Tallenna</Button>
            <Button 
                component={Link}
                to="/lista"
                variant="contained"
                fullWidth
            >Peruuta</Button>
            </form>
    )
}
export default Muokkaa;