import { Container, Typography } from "@mui/material";
import { List, ListItem, ListItemText, ListItemIcon, IconButton } from "@mui/material";
import Tooltip from '@mui/material/Tooltip';
import EditIcon from '@mui/icons-material/Edit';
import ClearIcon from '@mui/icons-material/Clear';
import { Link } from "react-router-dom";

function VitsiLista (props) {
    return(
        <Container style={{padding:"40px"}}>
            <Typography variant="h5">Kaikki kirjatut vitsit: </Typography>
            <List>
                {props.vitsit.map( (vitsit, idx) => {
                    return (
                        <ListItem key={idx}>
                            <ListItemIcon>
                            <Tooltip title="Muokkaa">
                                <IconButton 
                                    edge="end"
                                    component={Link}
                                    to={`/muokkaa/${vitsit.id}`}>
                                    <EditIcon/>
                                </IconButton>
                            </Tooltip>
                            </ListItemIcon>
                            <ListItemText 
                                primary={vitsit.kysymys}
                                secondary={
                                    vitsit.vastaus
                                    }/>
                            <ListItemIcon>
                            <Tooltip title="Poista">
                                <IconButton 
                                    edge="end"
                                    component={Link}
                                    to={`/poista/${vitsit.id}`}>
                                        <ClearIcon/>
                                </IconButton>
                                </Tooltip>
                            </ListItemIcon>
                        </ListItem>
                        );
                } ) }
            </List>
        </Container>
    );
}
export default VitsiLista