import { Button, Typography } from '@mui/material';
import { Link,  useParams, useNavigate } from 'react-router-dom';

function Poista(props) {

    const navigoi = useNavigate();
    const { id } = useParams();

    const poistettavaVitsi = props.vitsit.filter((vitsit) => {
                                return (vitsit.id === id);
                            })[0];

    const kasitteleLomake = (e) => {

        e.preventDefault();
        let vitsitApu = props.vitsit.filter((vitsi) => {
            return (vitsi.id !== id);
        });
        props.setVitsit([...vitsitApu]);
        navigoi("/lista");
    }

    return (
        <form onSubmit={kasitteleLomake}>
        <Typography
            style={{marginBottom : 10}}
            >Haluatko varmasti poistaa vitsin: {poistettavaVitsi.kysymys} ?
        </Typography>

        <Button 
            type="submit"
            variant='contained'
            
            fullWidth
            style={{marginBottom : 10}}
        >Poista vitsi</Button>
        <Button 
            component={Link}
            to="/lista"
            variant='contained'
            fullWidth
        >Peruuta</Button>
        </form>
    )
}
export default Poista;