import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import { Container } from '@mui/material';
import {Link} from 'react-router-dom';

function Valikko() {
    return(
        <Container  style={{textAlign:"center"}}>
            <ButtonGroup variant="contained">
                <Button  component={Link} to ="/">Etusivu</Button>
                <Button  component={Link} to ="/uusivitsi">Lisää vitsi</Button>
                <Button  component={Link} to ="/randomvitsi">Vitsikortit</Button>
                <Button  component={Link} to ="/lista">Lista</Button>
            </ButtonGroup>
        </Container>
    );
}
export default Valikko;