import { useRef, useState } from 'react';
import { Button, Container, TextField, Typography } from '@mui/material';
import { Link } from 'react-router-dom';
import {v4 as uuid }from 'uuid';

function UusiVitsi(props) {

    const [virheilmoitus, setVirheilmoitus] = useState({});
    const lomake = useRef({});
    
    const kasitteleLomake = (e) => {
        e.preventDefault();
        let virhe = {};
           
        if (!lomake.current.kysymys) {
            virhe = {...virhe, kysymys : "kysymys puuttuu"}
        } 
        if (!lomake.current.vastaus) {
            virhe = {...virhe, vastaus : "vastaus puuttuu"}
        }
        if (Object.entries(virhe).length > 0) {
            setVirheilmoitus({...virhe});
        } else {
            setVirheilmoitus({}); 
            let uusiVitsi = {   
                                kysymys : lomake.current.kysymys,
                                vastaus: lomake.current.vastaus,
                                id : uuid()
                            }            
            props.setVitsit([...props.vitsit, uusiVitsi]);
            }
        }

    const syoteKasittelija = (e) => {
        let arvo = e.target.value;
        lomake.current[e.target.name] = arvo;
        }

    return(
        <Container style={{padding:"40px", textAlign:"center"}}>
            <Typography variant='h5'>Lisää uusi vitsi</Typography>
            <form onSubmit={kasitteleLomake}>
            <TextField 
                label="Kysymys"
                name="kysymys"
                variant="outlined"
                fullWidth={true}
                style={{marginBottom : 10, marginTop: 10}}
                error={Boolean(virheilmoitus.kysymys)}
                helperText={virheilmoitus.kysymys}
                onChange={ syoteKasittelija }
                multiline
            />
            <TextField 
                label="Vastaus"
                name="vastaus"
                variant="outlined"
                fullWidth={true}
                style={{marginBottom : 10, marginTop: 10}}
                error={Boolean(virheilmoitus.vastaus)}
                helperText={virheilmoitus.vastaus}
                onChange={ syoteKasittelija }
                multiline
            />
            <Button 
                type="submit"
                variant="contained"
                fullWidth
                style={{marginBottom : 10}}
            >Lisää vitsi</Button>
            <Button 
                component={Link}
                to="/"
                variant="contained"
                fullWidth
            >Takaisin</Button>
            </form>
            </Container>
    );
}
export default UusiVitsi;